import {IonSpinner} from '@ionic/react';
import React, {useEffect, useState} from 'react';
import {observer} from "mobx-react-lite";
import {useStores} from "../../models/root-store/root-store-context";
import {RUTAS_APP_FABRICANTE} from "../../constants";
import {RouteComponentProps} from "react-router";
import {useOpenPage} from "../../helpers-ts";
import * as FIcon from "react-feather";
import {PageApp} from '../../components/PageApp';

export const FabricanteEstadisticas: React.FC<RouteComponentProps> = observer(({history}) => {
  const rootStore = useStores()
  const {loginStore} = rootStore
  const [Loading, setLoading] = useState(false)
  const openPage = useOpenPage()
  const [ProductosDisponibles, setProductosDisponibles] = useState(0)
  const [OrdenesPendientes, setOrdenesPendientes] = useState(0)
  const [OrdenesRetirar, setOrdenesRetirar] = useState(0)
  const [OrdenesFinalizadas, setOrdenesFinalizadas] = useState(0)

  useEffect(() => {
    setLoading(true)
    obtenerDatosInicio()
  }, [])

  const obtenerDatosInicio = () => {
    rootStore.environment.api.inicioDashboard().then(result => {
      setLoading(false)
      if (result.kind === 'ok') {
        setProductosDisponibles(result.data.productos_disponibles_cantidad)
        setOrdenesPendientes(result.data.ordenes_pendientes_cantidad)
        setOrdenesRetirar(result.data.ordenes_retirar_cantidad)
        setOrdenesFinalizadas(result.data.ordenes_finalizadas)
      }
    })
  }

  return <PageApp
    pageStyles={'background-blanco'}
    accion_boton_derecho={() => history.goBack()}
    boton_derecho={<FIcon.X size={30} color={'white'}/>}
    titulo_header={`Estadísticas`}
    sub_titulo={` `}
    mostrar_barra={false}
  >
    <div className={'pedidos-list-container py-2'}>
      {Loading && <IonSpinner name="crescent" className={'loadingSpinnerFabricanteApp pt-5'}/>}
      {!Loading &&
        <>
          <div className={'col-12 first-card-fabricante'} onClick={openPage(RUTAS_APP_FABRICANTE.CATALOGO)}>
            <div className={'card card-body rounded'}>
              <h3>Productos disponibles</h3>
              <div className={'d-flex mt-3'}>
                <FIcon.Package color={'#303AB2'} size={30}/>
                <h2 className={'ml-auto'}>{ProductosDisponibles}</h2>
              </div>
            </div>
          </div>
          <div className={'col-12'}
               onClick={() => history.push({pathname: RUTAS_APP_FABRICANTE.VENTAS, state: {tipo: 'pendiente'}})}>
            <div className={'card card-body rounded'}>
              <h3>Pedidos pendientes</h3>
              <div className={'d-flex mt-3'}>
                <FIcon.Box color={'#303AB2'} size={30}/>
                <h2 className={'ml-auto'}>{OrdenesPendientes}</h2>
              </div>
            </div>
          </div>
          <div className={'col-12'}
               onClick={() => history.push({pathname: RUTAS_APP_FABRICANTE.VENTAS, state: {tipo: 'retirar'}})}>
            <div className={'card card-body rounded'}>
              <h3>Pedidos a retirar</h3>
              <div className={'d-flex mt-3'}>
                <FIcon.CreditCard color={'#303AB2'} size={30}/>
                <h2 className={'ml-auto'}>{OrdenesRetirar}</h2>
              </div>
            </div>
          </div>
          <div className={'col-12'}
               onClick={() => history.push({pathname: RUTAS_APP_FABRICANTE.VENTAS, state: {tipo: 'finalizadas'}})}>
            <div className={'card card-body rounded'}>
              <h3>Pedidos entregados</h3>
              <div className={'d-flex mt-3'}>
                <FIcon.PlusSquare color={'#303AB2'} size={30}/>
                <h2 className={'ml-auto'}>{OrdenesFinalizadas}</h2>
              </div>
            </div>
          </div>
        </>
      }
    </div>
  </PageApp>;
});

export default FabricanteEstadisticas;
